/**
 * account detail page
 */
/* eslint-disable */
import React from 'react';
import ReactTable from 'react-table';
//api connect
import ConnectAPI from "../../../connectAPI";


class AdminAccountDetails extends React.Component {

   constructor(props){
      super(props);
      this.state = {
         
      };
      const columns = [
         {
            maxWidth: 75,
            Header: 'No.',
            accessor: 'count'
         },
         {
            sortable: false,
            Header: 'Invoice Id',
            accessor: 'id',
   
         },
         {
            minWidth: 160,
            Header: 'Buyer',
            accessor: 'buyer',
         },
         {
            Header: 'Date',
            accessor: 'date',
         },
         {
            Header: 'Price',
            accessor: 'price',
         },
         {
            Header: 'Payment',
            accessor: 'payment_type',
         },
         {
            Header: 'Status',
            accessor: 'status',
         },
         {
            Header: 'action',
            accessor: 'action',
            Cell: props => {
               return (
                  <div>
                     <InvoicePopup />
                     <Button
                        className="action-btn"
                        onClick={() => this.onDeleteCartItem(props.original)}
                     >
                        <i className="material-icons active-color">delete</i>
                     </Button>
                  </div>
               )
            },
         }
      ]
   }

   
 
   componentDidMount() {
      var conn = new ConnectAPI();
      conn.getFetch('user', '')
      .then(data => {
         console.log(data);
      });
   }
   
   render() {
         return (
            <div className="inner-container">
               <div className="iron-invoice-list-wrap">
                  <div className="iron-shadow rounded p-20 bg-base">
                     
                  </div>
               </div>
            </div>
         )
      }
}
export default AdminAccountDetails


